import React from 'react'
import { Link, useParams } from 'react-router-dom';
import axios from "axios";
import { useEffect } from 'react';
import { useState } from 'react';
import { PacmanLoader } from 'react-spinners';

export default function PlatformGames() {
    let {id} = useParams()
    console.log(id);
    const [platformGames, setPlatformGames] = useState(null)
    const [platformSelect, setPlatformSelect] = useState(id)
   

const gamesOfPlatform = {
  method: 'GET',
  url: 'https://opencritic-api.p.rapidapi.com/game',
  params: {platforms: `${platformSelect}`},
  headers: {
    'X-RapidAPI-Key': '0302d09901mshea068f403435a21p10d94djsn658e60574af4',
    'X-RapidAPI-Host': 'opencritic-api.p.rapidapi.com'
  }
};
async function choosePlatform(e){
  let platformValue = await e.target.getAttribute("value");
  setPlatformSelect(platformValue)
}
async function getPlatForms() {
    let { data } = await axios.request(gamesOfPlatform)
    setPlatformGames(data)
    console.log(data);
  }
  useEffect(() => {
    getPlatForms()
  }, [platformSelect])
  

  return (<>
  {platformGames != null? <> <div className='container'>
  <div className="dropdown w-100 text-center pb-3">
  <button className="btn btn-outline-light dropdown-toggle" type="button" data-bs-toggle="dropdown" aria-expanded="false">
    Platform
  </button>
  <ul className="dropdown-menu">
    <li onClick={choosePlatform} value="all" className="dropdown-item text-black">all</li>
    <li onClick={choosePlatform} value="XBXS" className="dropdown-item text-black">XBXS</li>
    <li onClick={choosePlatform} value="PS5" className="dropdown-item text-black">PS5</li>
    <li onClick={choosePlatform} value="Switch" className="dropdown-item text-black">Switch</li>
    <li onClick={choosePlatform} value="PC" className="dropdown-item text-black">PC</li>
    <li onClick={choosePlatform} value="XB1" className="dropdown-item text-black">XB1</li>
    <li onClick={choosePlatform} value="PS4" className="dropdown-item text-black">PS4</li>
    <li onClick={choosePlatform} value="Oculus" className="dropdown-item text-black">Oculus</li>
    <li onClick={choosePlatform} value="PSVR" className="dropdown-item text-black">PSVR</li>
    <li onClick={choosePlatform} value="Vive" className="dropdown-item text-black">Vive</li>
    <li onClick={choosePlatform} value="Wii-U" className="dropdown-item text-black">Wii-U</li>
    <li onClick={choosePlatform} value="3DS" className="dropdown-item text-black">3DS</li>
    <li onClick={choosePlatform} value="Vita" className="dropdown-item text-black">Vita</li>
  </ul>
</div>
    <h2 className='fw-bold py-4'>{platformSelect}</h2>
    <div className='row'>
      {platformGames.map((elem,idx) => <div key={idx} className='col-md-3'>
        <Link to={`/game/${elem.id}`}>
        <div>
          <figure>
            <img className='w-100 rounded-2 imagescalechang' src={"https://img.opencritic.com/" + elem.images.banner.og} alt={elem.name} />
          </figure>
          <figcaption className='text-center'>
            <h6>{elem.name}</h6>
          </figcaption>
        </div>
        </Link>
      </div>)}
    </div>
    </div>
    </>: <div className='vh-100 d-flex justify-content-center align-items-center'>
        <PacmanLoader color="white" />
      </div>}
  </>
    
  )
}
