import React from 'react'
import axios from "axios"
import { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { PacmanLoader } from 'react-spinners';
import { Swiper, SwiperSlide } from "swiper/react";
import { CircularProgressbar, buildStyles} from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';

import "swiper/css";
import "swiper/css/effect-fade";
import "swiper/css/navigation";
import "swiper/css/pagination";


import { Autoplay, EffectFade, Mousewheel, Navigation, Pagination } from "swiper";

export default function GameDetails() {
    let {id} = useParams()
    const [gameData, setGameData] = useState(null)

const game = {
  method: 'GET',
  url: `https://opencritic-api.p.rapidapi.com/game/${id}`,
  headers: {
    'X-RapidAPI-Key': '0302d09901mshea068f403435a21p10d94djsn658e60574af4',
    'X-RapidAPI-Host': 'opencritic-api.p.rapidapi.com'
  }
};
async function getGameDetail(){
    let {data} = await axios.request(game)
    setGameData(data)
}
useEffect(() => {
    getGameDetail()
  }, [])
  return <>
    {gameData !=null? <>
    <div className='container py-3'>
    {gameData.images.masthead == undefined? <img className='rounded-4 w-100' src={"https://img.opencritic.com/" + gameData.images.banner.og} alt="gameimage" /> : <img className='rounded-4 w-100' src={"https://img.opencritic.com/" + gameData.images.masthead.og} alt="gameimage" />}   
    </div>
    <div className='container'>
      <div className='row'>
        <div className='col-md-6 col-12'>
          <div>
            <h2 className='text-center'>Trailers</h2>
          </div>
          <Swiper
                slidesPerView={1}
                spaceBetween={10}
                slidesPerGroup={1}
                loop={false}
                loopFillGroupWithBlank={true}
                mousewheel={true}
                pagination={{
                  clickable: true,
                }}
                navigation={false}
                modules={[Mousewheel, Navigation ,Pagination]}
                className="mySwiper py-4"
              >
                {/* ana wa2f 3nd hetet el trailer bta3 el3dad*/}
                {gameData.trailers.length == 0?
                "":gameData.trailers.length == 1? <SwiperSlide>
                <div><iframe className='rounded-3 w-100' height="315" src={`https://www.youtube.com/embed/${gameData.trailers[0].videoId}`} title="YouTube video player" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe></div>
                </SwiperSlide>: gameData.trailers.length == 2? <><SwiperSlide>
                <div><iframe className='rounded-3 w-100' height="315" src={`https://www.youtube.com/embed/${gameData.trailers[0].videoId}`} title="YouTube video player" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe></div>
                </SwiperSlide>
                <SwiperSlide>
                <div><iframe className='rounded-3 w-100' height="315" src={`https://www.youtube.com/embed/${gameData.trailers[1].videoId}`} title="YouTube video player" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe></div>
                </SwiperSlide></> :<><SwiperSlide>
                <div><iframe className='rounded-3 w-100' height="315" src={`https://www.youtube.com/embed/${gameData.trailers[0].videoId}`} title="YouTube video player" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe></div>
                </SwiperSlide>
                <SwiperSlide>
                <div><iframe className='rounded-3 w-100' height="315" src={`https://www.youtube.com/embed/${gameData.trailers[1].videoId}`} title="YouTube video player" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe></div>
                </SwiperSlide>
                <SwiperSlide>
                <div><iframe className='rounded-3 w-100' height="315" src={`https://www.youtube.com/embed/${gameData.trailers[2].videoId}`} title="YouTube video player" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe></div>
                </SwiperSlide></>}
              </Swiper>
        </div>
        <div className="col-md-6 col-12">
         <div className="container">
           <div className='row'>
          <div><h2>{gameData.name}</h2></div>
          <div className="col-6">
          <div>
            {gameData.Companies.map((elem,idx)=> <p key={idx}>{elem.name}</p>)}
            <p>{gameData.firstReleaseDate.replace("T00:00:00.000Z"," ")}</p>
            {gameData.Platforms.map((elem,idx)=> <p className='d-inline-block pe-3 fw-bold' key={idx}>{elem.name}</p>)}
            <div>{gameData.Genres.map((elem,idx)=> <p className='d-inline-block p-2 me-3 rounded-3 genere fw-bold' key={idx}>{elem.name}</p>)}</div>
          </div>
          </div>
          <div className="col-6">
            <div className='w-25'>
            <CircularProgressbar styles={buildStyles({textColor: "#09C6F9",pathColor: "#09C6F9",trailColor: "white"})}  value={gameData.topCriticScore.toFixed(1)} text={`${gameData.topCriticScore.toFixed(1)}%`} />
            <p>CriticScore</p>
            <CircularProgressbar styles={buildStyles({textColor: "#09C6F9",pathColor: "#09C6F9",trailColor: "white"})}  value={gameData.percentRecommended.toFixed(1)} text={`${gameData.percentRecommended.toFixed(1)}%`} />
            <p className='text-center'>percent Recommended</p>
</div>
          </div>
          </div>
         </div>
         <p>{gameData.description}</p>
        </div>
      </div>
    </div>
    </>:<div className='vh-100 d-flex justify-content-center align-items-center'>
        <PacmanLoader color="white" />
      </div>}
  </>
}
