import jwtDecode from 'jwt-decode';
import React, { useEffect, useState } from 'react';
import { MoonLoader } from 'react-spinners';


export default function Profile() {
    const [loggedIn, setLoggedIn] = useState(null)
  function getUserToken(){
    let userToken = localStorage.getItem('tkn')
    let userData = jwtDecode(userToken);
    setLoggedIn(userData);
  }
  useEffect(() => {
    getUserToken()
  
  }, [])
  
  return <>
  {loggedIn != null? <>
  {/*<div className='container'>
  <h1 className='ps-2'>Profile</h1>
  <div className='row align-items-center justify-content-center'>
    <div className='col-md-3'>
    <div>
       <img className='bg-white w-100 rounded-5 me-3' src="/profile/profile-icon-9.png" alt="profile" />
  </div>
    </div>
    <div className="col-md-9">
      <div style={{"fontFamily":"Cairo"}}>
         <h4 className='pb-3'>Name: {loggedIn.first_name} {loggedIn.last_name}</h4>
         <h4 className='pb-3'>Age: {loggedIn.age}</h4>
         <h4 className='pb-3'>E-mail: {loggedIn.email}</h4>
       </div>
       </div>
  </div>
  </div>*/}
    <div className="card">
      <div className="card-header cardh1">
            <div className="card-header-bar">
              <a className="btn-message"><span className="sr-only">Message</span></a>
              <a className="btn-menu"><span className="sr-only">Menu</span></a>
            </div>

            <div className="card-header-slanted-edge">
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 200"><path className="polygon" d="M-20,200,1000,0V200Z" /></svg>
                <a className="btn-follow"><span className="sr-only">Follow</span></a>
            </div>
      </div>

      <div className="card-body">
          <h2 className="name text-dark">{loggedIn.first_name} {loggedIn.last_name}</h2>
          <h4 className="job-title fw-bold">Age: {loggedIn.age}</h4>
          <div className="bio fw">Email: {loggedIn.email}</div>
          <div className="social-accounts">
            <a><img src="https://res.cloudinary.com/dj14cmwoz/image/upload/v1491077480/profile-card/images/dribbble.svg" alt=""/><span className="sr-only">Dribbble</span></a>
            <a><img src="https://res.cloudinary.com/dj14cmwoz/image/upload/v1491077480/profile-card/images/twitter.svg" alt=""/><span className="sr-only">Twitter</span></a>
            <a><img src="https://res.cloudinary.com/dj14cmwoz/image/upload/v1491077480/profile-card/images/instagram.svg" alt=""/><span className="sr-only">Instagram</span></a>
          </div>
      </div>

      
  </div>

  </>:<div className='vh-100 d-flex justify-content-center align-items-center'>
    <MoonLoader color="white"/>
    </div>}

</>
}
