import React from 'react'
import axios from "axios"
import { useState } from 'react';
import { useEffect } from 'react';
import $ from "jquery";
import { Link } from 'react-router-dom';
import { PacmanLoader } from 'react-spinners';


export default function BrowseGames() {

  const [platForms, setPlatForms] = useState(null)

const Platforms = {
  method: 'GET',
  url: 'https://opencritic-api.p.rapidapi.com/platform',
  headers: {
    'X-RapidAPI-Key': '0302d09901mshea068f403435a21p10d94djsn658e60574af4',
    'X-RapidAPI-Host': 'opencritic-api.p.rapidapi.com'
  }
};

async function getPlatForms() {
  let { data } = await axios.request(Platforms)
  setPlatForms(data.filter(elem => elem.shortName != "Stadia"))

}
useEffect(() => {
  getPlatForms();
}, [])



  return (<>
  {platForms != null? <>
  <div className='container my-2'>
    <h2 className='text-center my-4 fw-bold'>Select Platform</h2>
    <div className="row gx-3 gy-4 justify-content-center align-items-center">
    <div className='bg-white mx-4 col-md-3 col-12 rounded-2 shad'>
    <Link to={`/browse/all`}>
    <div>
      <figure>
        <img className='w-100' src="https://img.opencritic.com/platforms/all.png" alt="" />
      </figure>
      <figcaption className='text-center'>
        <h4 className='text-dark fw-bold'>All Platforms</h4>
      </figcaption>
    </div>
    </Link>
  </div>
    {platForms.map((elem,idx) => <div className='bg-white mx-4 col-md-3 col-12 rounded-2 shad' key={idx}>
    <Link to={`/browse/${elem.shortName}`}>
    <div>
      <figure>
        <img className='w-100' src={"https://img.opencritic.com/"+elem.imageSrcV2} alt={elem.name} />
      </figure>
      <figcaption className='text-center'>
        <h4 className='text-dark fw-bold'>{elem.name}</h4>
      </figcaption>
    </div>
    </Link>
  </div>)}
    </div>
  </div>

  </> : <div className='vh-100 d-flex justify-content-center align-items-center'>
        <PacmanLoader color="white" />
      </div>}
  </>
  )
}
