import React, { useEffect } from 'react'
import axios from "axios"
import { useState } from 'react';
import { PacmanLoader } from 'react-spinners';
import { Swiper, SwiperSlide } from "swiper/react";
import { CircularProgressbar, buildStyles} from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';


import "swiper/css";
import "swiper/css/effect-fade";
import "swiper/css/navigation";
import "swiper/css/pagination";


import { Autoplay, EffectFade, Mousewheel, Navigation, Pagination } from "swiper";
import { Link } from 'react-router-dom';

export default function Home() {

  const [popGames, setPopGames] = useState(null)
  const [recent, setRecent] = useState(null)
  const [upComing, setUpComing] = useState(null)
  const [hallOfFame, setHallOfFame] = useState(null)
  const [psGames, setPsGames] = useState(null)
  const [yearChange, setYearChange] = useState(2022)
  const percentage = 66;

  const options = {
    method: 'GET',
    url: 'https://opencritic-api.p.rapidapi.com/game/popular',
    headers: {
      'X-RapidAPI-Key': '0302d09901mshea068f403435a21p10d94djsn658e60574af4',
      'X-RapidAPI-Host': 'opencritic-api.p.rapidapi.com'
    }
  };

  const  psexclusive = {
    method: 'GET',
    url: 'https://opencritic-api.p.rapidapi.com/game',
    params: {platforms: 'ps4,ps5'},
    headers: {
      'X-RapidAPI-Key': '0302d09901mshea068f403435a21p10d94djsn658e60574af4',
      'X-RapidAPI-Host': 'opencritic-api.p.rapidapi.com'
    }
  };


  const recentreleased = {
    method: 'GET',
    url: 'https://opencritic-api.p.rapidapi.com/game/recently-released',
    headers: {
      'X-RapidAPI-Key': '0302d09901mshea068f403435a21p10d94djsn658e60574af4',
      'X-RapidAPI-Host': 'opencritic-api.p.rapidapi.com'
    }
  };

  const newgames = {
    method: 'GET',
    url: 'https://opencritic-api.p.rapidapi.com/game/upcoming',
    headers: {
      'X-RapidAPI-Key': '0302d09901mshea068f403435a21p10d94djsn658e60574af4',
      'X-RapidAPI-Host': 'opencritic-api.p.rapidapi.com'
    }
  };


  async function chooseYear(e){
    let yearValue = await e.target.getAttribute("value");
    setYearChange(yearValue)
  }
  
  const hallOfFameNow = {
    method: 'GET',
    url: `https://opencritic-api.p.rapidapi.com/game/hall-of-fame/${yearChange}`,
    headers: {
      'X-RapidAPI-Key': '0302d09901mshea068f403435a21p10d94djsn658e60574af4',
      'X-RapidAPI-Host': 'opencritic-api.p.rapidapi.com'
    }
  };
  

  async function getPopularGames() {
    let { data } = await axios.request(options)
    setPopGames(data)
  }
  async function getRecentGames() {
    let { data } = await axios.request(recentreleased)
    setRecent(data)
  }
  async function getUpComingGames() {
    let { data } = await axios.request(newgames)
    setUpComing(data)
  }
  async function getHallOfFame() {
    let { data } = await axios.request(hallOfFameNow)
    setHallOfFame(data)
  }
  async function getPsGames() {
    let { data } = await axios.request(psexclusive)
    console.log(data.map(elem => elem.Platforms.filter(x => x.shortName != "XBXS" && x.shortName != "XB1")))
    setPsGames(data)
  }
 
  useEffect(() => {
    getPopularGames();
    getRecentGames();
    getUpComingGames();
    getHallOfFame();
    getPsGames();
  }, [yearChange])
  return (
    <>
      {popGames && recent && upComing && hallOfFame && psGames != null ? <>
        <header>
          <Swiper
            spaceBetween={30}
            effect={"fade"}
            navigation={false}
            loop={true}
            autoplay={{
              delay: 2500,
              disableOnInteraction: false,
            }}
            pagination={{
              dynamicBullets: true,
            }}
            modules={[Autoplay, EffectFade, Navigation, Pagination]}
            className="mySwiper logo"
          >
            <SwiperSlide className='overflow-hidden'>
              <div className='text-center'>
                <img className='vh-100 w-100 position-relative overflow-hidden' src='./Homeimages/cod.jpg' />
                <div className='position-absolute layer d-flex justify-content-center align-items-center'>
                  <div>
                    <h1>Its Time To Play The Game</h1>
                  </div>
                </div>
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className='text-center overflow-hidden'>
                <img className='w-100 vh-100 position-relative ' src='./Homeimages/gamer.jpg' />
                <div className='position-absolute layer d-flex justify-content-center align-items-center'>
                  <div>
                    <h1>Know What Are The Popular Games</h1>
                  </div>
                </div>
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className='text-center overflow-hidden'>
                <img className='w-100 vh-100 position-relative ' src='./Homeimages/masterchife.jpg' />
                <div className='position-absolute layer d-flex justify-content-center align-items-center'>
                  <div>
                    <h1>Know The Games For Each Platform</h1>
                  </div>
                </div>
              </div>
            </SwiperSlide>
          </Swiper>
        </header>

        <section className='my-2'>
          <h2 className='text-center py-3'>Popular Games</h2>
          <div className='container'>
            <div className="row">
              <Swiper
                slidesPerView={4}
                spaceBetween={10}
                slidesPerGroup={4}
                loop={true}
                loopFillGroupWithBlank={true}
                mousewheel={true}
                pagination={{
                  clickable: true,
                }}
                modules={[Mousewheel, Navigation ,Pagination]}
                className="mySwiper py-3"
              >
                {popGames.map((elem, idx) => <SwiperSlide key={idx}><div className="skiptext col-md-12 col-sm-12">
                 <Link to={`/game/${elem.id}`}>
                 <div className='position-relative popimg'>
                    <div className="score rounded-circle p-1 zindex">
                      {elem.topCriticScore.toFixed(1)}
                    </div>
                    <div>
                    <img className='w-100 rounded-2' src={"https://img.opencritic.com/" + elem.images.box.og} alt="gameimage" />
                    <div className='layer3 position-absolute overflow-hidden'></div>
                    <h6 className='py-2 zindex'>{elem.name}</h6>
                    </div>
                  </div>
                  </Link>
                </div></SwiperSlide>)}
              </Swiper>

            </div>
          </div>
          <div className="container">
            <div className="row justify-content-around gy-5">
              <div className="col-md-5 col-12 bg-white text-dark bg-gradient rounded-5">
                <div className='overflow-hidden'>
                  <h3 className='text-center py-2'>RECENTLY RELEASED</h3>

                  {recent.map((elem, idx) => <Link className='text-black' key={idx} to={`/game/${elem.id}`}>
                    <div className='row noborder border border-bottom py-2 align-items-center'>
                    <div className='col-md-10 col-8'>
                    <h6 className='fw-bold'>{elem.name}</h6>
                    </div>
                    <div className='col-md-2 col-4'>
                      <div className='shadow-lg'>
                       {elem.images.box == undefined? <img className='w-100 rounded-2' src={"https://img.opencritic.com/" + elem.images.banner.og} alt="gameimage" /> : <img className='w-100 rounded-2' src={"https://img.opencritic.com/" + elem.images.box.og} alt="gameimage" />}
                      </div>
                    </div>
                  </div>
                  </Link>)}
                </div>
              </div>
              <div className="col-md-5 col-12 bg-white text-dark bg-gradient rounded-5">
                <div className='overflow-hidden'>
                  <h3 className='text-center py-2'>UPCOMING</h3>

                  {upComing.map((elem, idx) => <Link key={idx} className='text-black noborder' to={`/game/${elem.id}`}>
                    <div className='row noborder border border-bottom py-2 align-items-center'>
                    <div className='col-md-10 col-8'>
                      <h6 className='fw-bold'>{elem.name}</h6>
                    </div>
                    <div className='col-md-2 col-4'>
                      <div className='shadow-lg'>
                        <img className='w-100 rounded-2' src={"https://img.opencritic.com/" + elem.images.box.og} alt="gameimage" />
                      </div>
                    </div>
                  </div>
                  </Link>)}

                </div>
              </div>
            </div>
          </div>
        </section>
        <section>
        <div className='container'>
          <div className='row justify-content-center'>
            <div className="col-12">
            <div>
            <h2 className='text-center py-3'>{yearChange} Hall of Fame</h2>
        <div className="dropdown w-100 text-center pb-3">
  <button className="btn btn-outline-light dropdown-toggle" type="button" data-bs-toggle="dropdown" aria-expanded="false">
    Year
  </button>
  <ul className="dropdown-menu">
    <li onClick={chooseYear} value="2022" className="dropdown-item text-black">2022</li>
    <li onClick={chooseYear} value="2021" className="dropdown-item text-black">2021</li>
    <li onClick={chooseYear} value="2020" className="dropdown-item text-black">2020</li>
    <li onClick={chooseYear} value="2019" className="dropdown-item text-black">2019</li>
    <li onClick={chooseYear} value="2018" className="dropdown-item text-black">2018</li>
    <li onClick={chooseYear} value="2017" className="dropdown-item text-black">2017</li>
    <li onClick={chooseYear} value="2016" className="dropdown-item text-black">2016</li>
  </ul>
</div>
            </div>
            </div>
          </div>
        </div>
          <div className="container">
            <div className='row'>
              {hallOfFame.map((elem,idx) => <div key={idx} className="col-md-4">
              <Link to={`/game/${elem.id}`}>
              <figure className='position-relative'>
                    <img className='w-100 rounded-3' src={"https://img.opencritic.com/" + elem.images.banner.og} alt="gameimage" />
                    <div className='position-absolute layer2'>
                      <div className="container">
                      <div className="row">
                      <div className="col-12">
                        <figcaption className='fw-bold'>
                        <h5>{elem.name}</h5>
                        <div className='row align-items-center'>
                        <div className='col-6'>
                            <div><p>{elem.firstReleaseDate.replace("T00:00:00.000Z"," ")}</p></div>
                          </div>
                          <div className='col-6'>
                          <div className='ms-auto ratingprecent' ><CircularProgressbar styles={buildStyles({textColor: "#09C6F9",pathColor: "#09C6F9",trailColor: "white"})}  value={elem.topCriticScore} text={`${elem.topCriticScore}%`} /></div>
                          </div>
                        </div>
                      </figcaption>
                      </div>
                      </div>
                      </div>
                    </div>
              </figure>
              </Link>
              </div>)}
            </div>
          </div>
        </section>
        
      </> : <div className='vh-100 d-flex justify-content-center align-items-center'>
        <PacmanLoader color="white" />
      </div>}
    </>
  )
}
