import React from 'react';
import { Link } from 'react-router-dom';
import "./mynav.css"
import axios from "axios"
import { useState } from 'react';
import { useEffect } from 'react';
import $ from "jquery";





function NavBar() {
  const [searched, setSearched] = useState("")
  const [searchResult, getSearchResult] = useState(null);
  

const searchGame = {
  method: 'GET',
  url: 'https://opencritic-api.p.rapidapi.com/game/search',
  params: {criteria: `${searched}`},
  headers: {
    'X-RapidAPI-Key': '0302d09901mshea068f403435a21p10d94djsn658e60574af4',
    'X-RapidAPI-Host': 'opencritic-api.p.rapidapi.com'
  }
};

async function getSearchedGames() {
  let { data } = await axios.request(searchGame)
  getSearchResult(data)
  console.log(data);
  
}

function getSearchValue() {
  let searchValue = $('#search')[0].value;
  if (searchValue != null) {
    setSearched(searchValue)
  }
}
useEffect(() => {
 getSearchedGames()
}, [searched])


  return <>

<nav className="navbar navbar-expand-lg navbar-dark bg-black bg-gradient">
  <div className="container-fluid">
    <Link to="home" className="navbar-brand maintitle logo p-3 rounded-2 border" >G2K</Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav align-items-center me-auto mb-2 mb-lg-0 text-center">
        <li className="nav-item">
        
          <Link to="home" className="nav-link active navicons" aria-current="page">  
        <i className="fa-solid fa-house fa-2x"></i>
            <h6>Home</h6>
          </Link>
        </li>
        <li className="nav-item">
          <Link to="browse" className="nav-link active navicons" aria-current="page">
          <i className="fa-solid fa-gamepad fa-2x"></i>
            <h6>Browse</h6>
          </Link>
        </li>
      </ul>
      <div className="container ">
      <div>
        <input  onChange={getSearchValue} autoComplete="off" className='form-control bg-light text-dark my-3 ' type="text" id="search" placeholder='Search Games' />
      </div>
      
      {searched == null &&  searched != "" && $('#search')[0].value != ""? <ul className="dropdown-menu">
  </ul>:<ul className="searchul dropdown-menu show">
  {searched != null && searched != ""? searchResult.map((elem,idx) => <Link onClick={() =>{$('.searchul').removeClass("show")}} to={`/game/${elem.id}`} key={idx}><li className="dropdown-item text-black">{elem.name}</li></Link>
  ): ""}
      </ul>}
      </div>
      
      <ul className="navbar-nav ms-auto mb-2 align-items-center mb-lg-0">
        <li className='nav-item'>
        <i className="fa-brands me-3 fa-facebook"></i>
        <i className="fa-brands me-3 fa-twitter"></i>
        <i className="fa-brands me-3 fa-spotify"></i>
        <i className="fa-brands me-3 fa-instagram"></i>
        </li>
      </ul>
    </div>
  </div>
</nav>
  
</>
}

export default NavBar;


